@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,700;1,400;1,700&display=swap);
html {
  box-sizing: border-box;
  overflow: hidden;
}

*,
*:before,
*:after {
  margin: 0;
  box-sizing: inherit;
}

body {
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
}

#root {
  min-width: 100vw;
  min-height: 100vh;
}


main {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  height: calc(100vh - 60px);
}

main > div {
  height: 100%;
  min-width: 50%;
  border: 3px solid #030a18;
  padding: 10px;
}

main.side-by-side {
  -webkit-flex-direction: row;
          flex-direction: row;
}

main.side-by-side > div {
  width: 100%;
  min-height: 50%;
}

header {
  background-color: #030a18;
  min-height: 60px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  color: #fff3ae;
  padding: 10px;
}

button {
  height: 40px;
  width: 40px;
  font-size: 30px;
  background-color: #fff3ae;
  border: 0;
  border-radius: 3px;
  margin: 5px;
  padding: 0;
}

button:focus {
  outline: none;
}

button:hover {
  cursor: pointer;
}

button.active {
  border: 2px solid rgb(255, 115, 0);
  outline: -webkit-focus-ring-color auto 1px;
}

button.active:hover {
  cursor: default;
}

svg {
  vertical-align: text-bottom;
}
#editor {
  height: 100%;
  min-width: 50%;
  border: 3px solid #030a18;
  padding: 10px;
  background-color: #2e3035;
  font-family: 'Courier New', Courier, monospace;
  font-size: 14px;
  color:#fcf8e0;
  resize: none;
  outline: none;
}
#preview {
  overflow-y: scroll;
}

#preview > * {
  margin-bottom: 15px;
}

